<template>
  <div class="divError">
    <div class="content">
      <el-image :src="icon"></el-image>
      <p>{{text}}</p>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {

    }
  },
  computed: {
    icon: function () {
      const query = this.$route.query;
      let code = query.code;
      if (code == undefined) code = 404;
      return require("@/assets/" + code + ".png");
    },
    text: function () {
      return "请刷新重试";
    }
  }
}
</script>

<style lang="less" scoped>
.divError {
  width: 100%;
  height: 100%;
  position: relative;
  .content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>